import http from '@/api/http.js'
import QSPopup from '@/components/QS-popup/QS-popup.vue';
export default {
	components: {QSPopup},
	data() {
		return {
			orderinfo:{},
			pointSelect:false,
			orderid:'',
			chooseList:[
				{name:'支付宝支付',src:'/static/zfb.png'},
				{name:'微信支付',src:'/static/wx.png'},
				{name:'银联云闪付',src:'/static/ysf.png'},
				{name:'余额支付',src:'/static/ye.png'},
			],
			selectImg:'/static/select.png',
			unSelectImg:'/static/unselect.png'
		}
	},
	onLoad(option) {
		this.chooseSel();
		this.orderid = option.id;
		this.getorder(this.orderid);
	},
	methods:{
		getorder(orderid){
			let data = {
				order_id:orderid,
				token:uni.getStorageSync('token'),
			};
			this.$util.showLoading('请稍后');
			http('/api/Order/orderInfo','post',data).then(res=>{
				// console.log(res.data.data.bannerList.data)
				this.orderinfo = res.data.data;
				console.log(this.orderinfo);
				this.$util.hideLoading();
			})
		},
		show(ref) {
			this.$refs[ref].show();
		},
		hides(ref){
			this.$refs[ref].hide()
		},
		pointSelectTap(){
			this.pointSelect = !this.pointSelect
		},
		tipSend(){
				//type: 1提醒发货 2确认收货
			let data = {
				type: 1,
				token: uni.getStorageSync('token'),
				ordernum: this.orderinfo.ordernum
			};
			http('/api/user/deliverGoodsStatus ', 'post', data).then(res => {  
				// console.log(res.data.data.bannerList.data)
				this.$util.showToast('提醒成功');
				
			});
			
		},
		choose(type, indexs) {
			let that = this
			let cartList = that.chooseList.map((item, index) => {
				item.checked = false;
				
				if (indexs == index) {
					item.checked = true;
				}
				return item;
			});
			that.chooseList = cartList;
		},
		chooseSel(){
			let cartList = this.chooseList.map((item, index) => {
				item.checked = false;
				if (index == 0) {
					item.checked = true;
				}
				return item;
			});
		}
	}
}
